<template>
  <div class="container mt-5">
    <h2 class="mb-4">가게 등록</h2>
    <div v-if="shouldShowFileRegistrationSection" class="alert alert-info">
      <B_ShopReg :mnum="mnum" />
    </div>
    <div
      v-else-if="
        // shopStatus &&
        // shopStatus.bstaus_mikey === 501 &&
        bFiles.bfstatus_mikey === 601
      "
      class="alert alert-info"
    >
      가게 등록 승인 중입니다.
    </div>
    <div
      v-else-if="shopStatus && shopStatus.bstaus_mikey === 502"
      class="alert alert-success"
    >
      가게 등록이 승인 되었습니다.
    </div>
    <B_FileInsert v-if="shouldShowFileInsertSection" :mnum="mnum" />
  </div>
</template>

<script>
import axios from "axios";
import B_ShopReg from "@/components/business/MS_B_ShopReg.vue";
import B_FileInsert from "@/components/business/MS_B_FileInsert.vue";

export default {
  name: "B_Files",

  components: {
    B_ShopReg,
    B_FileInsert,
  },

  data() {
    return {
      shopStatus: {},
      bFiles: {},
      mnum: 1011, // mnum 데이터 추가
    };
  },

  mounted() {
    this.fetchData();
  },

  computed: {
    shouldShowFileRegistrationSection() {
      return !this.shopStatus || !this.shopStatus.bstaus_mikey || !this.mnum;
    },
    shouldShowFileInsertSection() {
      return !this.bFiles || !this.bFiles.bfnum;
    },
  },

  methods: {
    async fetchData() {
      try {
        const [shopStatusResponse, bFilesResponse] = await Promise.all([
          axios.post("/ShopAcceptStatus", { mnum: this.mnum }),
          axios.post("/BFileDetail", { mnum: this.mnum }),
        ]);

        this.shopStatus = shopStatusResponse.data;
        this.bFiles = bFilesResponse.data;
        console.log("상태:", this.shopStatus);
        console.log("파일 상태:", this.bFiles.bfnum);
      } catch (error) {
        console.error("데이터를 불러오는 중 오류 발생:", error);
      }
    },
  },
};
</script>

<style>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  margin-top: 10px;
}
</style>
